import {JSNodeBase, Identifier} from '../index';
import {createBuilder} from '../utils';

export type ContinueStatement =
  & JSNodeBase
  & {
    type: 'ContinueStatement';
    label?: Identifier;
  };

export const continueStatement = createBuilder<ContinueStatement>(
  'ContinueStatement',
  {
    bindingKeys: {},
    visitorKeys: {
      label: true,
    },
  },
);
