import Generator from '../../Generator';
import {
  FlowObjectTypeSpreadProperty,
  flowObjectTypeSpreadProperty,
  AnyNode,
} from '@romejs/js-ast';

export default function FlowObjectTypeSpreadProperty(
  generator: Generator,
  node: AnyNode,
) {
  node = flowObjectTypeSpreadProperty.assert(node);

  generator.token('...');
  generator.print(node.argument, node);
}
